.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background: #741188; */
  background: radial-gradient(
      circle farthest-corner at center center,
      #e3f6f7,
      #13383a
    )
    no-repeat;
  padding: 0 2rem;
}

.main-header h1 {
  color: white;
}
